<template>
    <div class="modal-layout">
    <div class="darken" @click="closeModal">
        <div class="new-modal liquidity-deposit-modal" @click.stop="">
                <button class="new-modal-back-button thin" @click="closeModal"></button>
            <div class="modal-content">
        <p class="window-title">{{$localizeFilter('depositing_funds_into_pool') + `\n${pool.leftCoinTicker}/${pool.rightCoinTicker}`}}</p>

        <p class="window-text">{{$localizeFilter('liquidity_deposit_modal_text_amount', ['USDT'])}}</p>
        <div class="input-field">
            <input id="amount" class="input-class" :placeholder="$localizeFilter('amount_in', ['USDT'])" type="text" v-model.trim="amount"  :class="{ invalid: v$.amount.$error }"/>
            <small class="error" v-for="(error, index) of v$.amount.$errors" :key="index">
                {{ error.$message }}
            </small>
        </div>

        <template v-if="!presale">
            <p class="window-text" v-html="$localizeFilter('enter_organization_identification_number')"></p>
            <div class="input-field">
                <input id="organizationIdentificationNumber" class="custom-input" :placeholder="$localizeFilter('organization_identification_number')" type="text" v-model.trim="organizationIdentificationNumber"  :class="{ invalid: v$.organizationIdentificationNumber.$error || organizationIsNotCWHolder }"/>
                <small class="error" v-for="(error, index) of v$.organizationIdentificationNumber.$errors" :key="index">
                    {{ error.$message }}
                </small>
                <small class="error server-error" v-if="organizationIsNotCWHolder" v-html="$localizeFilter('ORGANIZATION_IS_NOT_CW_HOLDER')"></small>
            </div>
        </template>
        
        <div class="modal-warning">
            <img class="warning-label" src="../../assets/newImages/warning-label.svg" alt="">
            <p class="window-text warning">{{$localizeFilter('liquidity_deposit_modal_warning', [pool.leftCoinTicker, pool.rightCoinTicker])}}</p>
        </div>

    </div>
    <button class="basic-button submit-button" @click="submitHandler" :class="submitInProgress ? 'inactive' : ''">{{$localizeFilter('deposit_funds')}}</button>

    </div>
    </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import useVuelidate from "@vuelidate/core";
import { helpers, required, numeric, minValue, maxValue, minLength, maxLength } from "@vuelidate/validators";
import localizeFilter from '@/filters/localize.filter'

function multiplyNumberByDecimals(value, decimalsNumber) {
    // const decimals = decimalsNumber.toString().length
    let [integer, fraction] = value.toString().split('.')
    // console.log(decimalsNumber)
    let res = 0n;
    if(fraction) {
        res += BigInt(fraction) * decimalsNumber;
        for( let i = 0; i < fraction.length; i++ ) {
            res /= BigInt(10);
        }
    }
    res += BigInt(integer) * decimalsNumber;
    return res;
}

export default defineComponent({
    props: {
        pool: {
            type: Object,
            required: true
        },
        testBalance: {
            type: Number,
            required: true
        }
    },
    validations() {
        return {
            amount: {
                // required: helpers.withMessage(({ 
                //     $pending,
                //     $invalid,
                //     $params,
                //     $model
                // })=>`${$pending} ${$invalid} ${$params} ${$model}`, required),
                required: helpers.withMessage(localizeFilter("field_must_not_be_empty"), required),
                numeric: helpers.withMessage(localizeFilter("field_must_be_numeric"), required),
                minValue: helpers.withMessage(localizeFilter('min_amount', [`${this.$utils.formatNumber(this.minAmount, 'en-US', 0, 0)} USDT`]), minValue(this.minAmount)),
                maxValue: helpers.withMessage(localizeFilter('not_enough_funds_on_balance',['USDT']), maxValue(this.maxAmount)),
            },
            organizationIdentificationNumber: {
                required: helpers.withMessage(localizeFilter("field_must_not_be_empty"), required)
            }
        };
    },
    setup() {
        return { v$: useVuelidate() };
    },
    data: () => ({
        amount: "",
        submitInProgress: false,
        organizationIdentificationNumber: null,
        organizationIsNotCWHolder: false,
    }),
    methods: {
        closeModal(reload) {
            this.$emit("closeModal", reload);
        },
        async submitHandler() {
            this.v$.$touch();
            if (this.v$.$error /*|| !this.conditionsAccepted*/) {
                if (this.presale && !this.v$.amount.$error) {
                    // continue
                } else {
                    return;
                }
            }

            this.submitInProgress = true
            // send data to store
            try {
             //   let amount = multiplyNumberByDecimals(this.amount, this.assetDecimalsMap[this.pool.leftCoinTicker]).toString();
                const data = {poolId: this.pool.poolId, amount: this.amount, poolType: 'crypto_fiat_pool', organizationIdentificationNumber: this.organizationIdentificationNumber}
                await this.$store.dispatch('depositFundsToLiquidityPool', data)
                this.$message(localizeFilter('depositing_funds_into_pool_successful'))
                this.$emit('closeModal')
                setTimeout(()=>{
                    this.submitInProgress = false
                },1000)
            } catch(e) {
                if (e.message === 'ORGANIZATION_IS_NOT_CW_HOLDER') {
                    this.organizationIsNotCWHolder = true
                } else {
                    this.$error(e)
                }
                setTimeout(()=>{
                    this.submitInProgress = false
                },1000)
            } 
        },
    },
    computed: {
        assetDecimalsMap() {
            return this.$store.getters.assetDecimalsMap;
        },
        maxAmount() {
            return this.testBalance / 1000000.0
        },
        presale(){
            // return false;
            return (this.pool.poolType=='crypto_fiat_pool' && this.pool.launchTimestamp > +Date.now());
        },
        minAmount() { // in USDT
            if (this.presale && !this.pool.deposited) {
                return 10000
            } else {
                return 1
            }
        }
    }
})
</script>


