            <!-- <AmountView v-if="selectedPoolType==='my_pools'" :title="$localizeFilter('amount_test')" :amount="testBalance" :color="'type-gray'"/>    -->
            <AmountView class="pool-amount-first amount-main" :title="$localizeFilter('gifts_to_referrals')" :amount="referralGiftsBalance" :type="'gift'" :color="'type-red'" :counterType="'gift'" :giftPercent="giftPercent" v-if="selectedPoolType==='my_pools'" @showGiftModal="showGiftModal=true"/>
        </div>
