<template>
    <div class="modal-layout">
    <div class="darken" @click="closeModal">
        <div class="new-modal liquidity-withdraw-modal" @click.stop="">
            <button class="new-modal-back-button thin" @click="closeModal"></button>
            <div class="modal-content">
                <p class="window-title">{{$localizeFilter('withdrawing_funds_from_pool') + ` ${pool.leftCoinTicker}/${pool.rightCoinTicker}`}}</p>

                <p class="window-subtitle">{{$localizeFilter('liquidity_withdraw_subtitle', [pool.depositedLeftCoin, pool.leftCoinTicker, pool.depositedRightCoin, pool.rightCoinTicker])}}</p>

        <!-- <WithdrawTypeToggle :class="'toggle-block'" :title="$localizeFilter('withdraw_type')" :variants="withdrawTypes" :prefix="'withdraw_type_'" @toggle="toggle"/> -->
        
                <div class="modal-warning">
                    <img class="warning-label" src="../../assets/newImages/warning-label.svg" alt="">
                    <p class="window-text warning">{{ (selectedWithdrawType === 'fast') ? $localizeFilter('withdraw_warning_fast') : $localizeFilter('withdraw_warning')}}</p>
                </div>

                <div class="conditions-block" @click="accept–°onditions">
                    <input type="checkbox" id="checkbox" class="custom-checkbox" v-model="conditionsAccepted" @click="accept–°onditions">
                    <label for="checkbox" class="conditions-label">{{$localizeFilter('withdraw_agreement')}}</label>
                </div>


        <!-- :class="conditionsAccepted ? '' : 'inactive'" -->


    </div>
        <div class="buttons-block">
            <button class="submit-button" :class="submitInProgress ? 'inactive' : ''" @click="submitHandler">{{$localizeFilter('liquidity_withdraw')}}</button>
            <button class="basic-button red-button" @click="cancel">{{$localizeFilter('cancel')}}</button>
        </div>
        <!-- <div class="blur">

        </div> -->
    </div>
