<script>
import { defineComponent } from '@vue/composition-api'
import localizeFilter from '@/filters/localize.filter'
import WithdrawTypeToggle from '../WithdrawTypeToggle.vue';

function multiplyNumberByDecimals(value, decimalsNumber) {
    // const decimals = decimalsNumber.toString().length
    let [integer, fraction] = value.toString().split('.')
    // console.log(decimalsNumber)
    let res = 0n;
    if(fraction) {
        res += BigInt(fraction) * decimalsNumber;
        for( let i = 0; i < fraction.length; i++ ) {
            res /= BigInt(10);
        }
    }
    res += BigInt(integer) * decimalsNumber;
    return res;
}

export default defineComponent({
    props: {
        pool: {
            type: Object,
            required: true
        }
    },
    data: () => ({
        conditionsAccepted: false,
        submitInProgress: false,
        selectedWithdrawType: 'default',
        withdrawTypes: ['default', 'fast'],
    }),
    components: {
        WithdrawTypeToggle
    },
    methods: {
        toggle(idx) {
            this.selectedWithdrawType = this.withdrawTypes[idx]
        },
        accept–°onditions() {
            this.conditionsAccepted = !this.conditionsAccepted
        },
        closeModal(reload) {
            this.$emit("closeModal", reload);
        },
        cancel() {
            this.closeModal()
        },
        async submitHandler() {
            if (!this.conditionsAccepted) {
                return;
            }

            this.submitInProgress = true

            // send data to store
            try {
                const data = {poolId: this.pool.poolId, withdrawType: this.selectedWithdrawType}
                await this.$store.dispatch('withdrawFundsFromLiquidityPool', data)
                this.$message(localizeFilter('withdraw_funds_from_pool_application_created_successful'))
                this.$emit('closeModal')
                setTimeout(()=>{
                    this.submitInProgress = false
                },1000)
            } catch(e) {
                this.$error(e)
                setTimeout(()=>{
                    this.submitInProgress = false
                },1000)
            } 
        },
    },
    computed: {
        assetDecimalsMap() {
            return this.$store.getters.assetDecimalsMap;
        },
    }
})
</script>


